/* eslint-disable jsx-a11y/anchor-is-valid */
import React,{ useEffect, useState} from 'react'

import {Link, withRouter} from 'react-router-dom'

import moment from 'moment'
import {mainConfig} from '../../../config/mainConfig'
let token = ''


const TagProductWrapper=(props: any)=> {


  const [loading, setloading] = useState<any>(false)

  const [Header, setHeader] = useState<any>('')
  const [tagProducts, setTagProducts] = useState<any[]>([])

  useEffect(() => {
    let data = props.location.state
    if (data != undefined) {
      localStorage.setItem('Tagid', data.tag_id)
    }
    let datamain = data != undefined ? data.tag_id : localStorage.getItem('Tagid')
    getSupplier(datamain)
  }, [])

  const getSupplier=(data1: any)=> {
    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/tag-with-all-products/${data1}`, {
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (response) {
          if (response.status != 200) {
            localStorage.clear()
            window.location.reload()
          }

          response.json().then(function (data) {
           
            setTagProducts(data.products)
            setHeader(data.tag[0].name)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {}
  }

  token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')

  return (
    <React.Fragment>
      {loading == true ? (
        <div style={{marginLeft: '50%'}}>
          <div className='spinner-border m-2' role='status'>
            <span className='sr-only'>Loading...</span>
          </div>
        </div>
      ) : (
        <React.Fragment>
          <ol className='breadcrumb breadcrumb-dot fs-6 fw-bold' style={{margin: '1%'}}>
            <Link to='/home' className='pe-1 cursor-pointer'>
              <li className='breadcrumb-item pe-1'>Home</li>
            </Link>
            / &nbsp;<li className='breadcrumb-item pe-3'>Tag-Product Details</li>
          </ol>

          <div className='row gy-5 g-xl-10' style={{marginTop: '1%'}}>
            <div className='col-xl-3'>
              <div className='card-xxl-stretch mb-5 mb-xxl-8'>
                <div className='row gy-5 g-xl-7'>
                  <div className={`card`}>
                    <div className='card-header border-0 pt-2' style={{alignSelf: 'center'}}>
                      <h3 className='card-title align-items-start flex-column'>
                        <span
                          className='card-label fw-bolder fs-2 mb-1'
                          style={{color: 'rgb(138, 38, 80)'}}
                        >
                          {Header}
                        </span>
                      </h3>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className='row g-5 gx-xxl-8'>
            <div className='col-xxl-12'>
              <div className='card-xxl-stretch mb-5 mb-xxl-8'>
                <div className='row gy-5 g-xl-4'>
                  <div className={`card`}>
                    <div className='card-header border-0 pt-2'>
                      <h3 className='card-title align-items-start flex-column'>
                        <span
                          className='card-label fw-bolder fs-3 mb-1'
                          style={{color: 'rgb(138, 38, 80)'}}
                        >
                          Products
                        </span>
                      </h3>
                      <div className='card-toolbar'></div>
                    </div>

                    <div className='card-body py-3 '>
                      <div className='table-responsive h-500px'>
                        <table className='table table-row-dashed table-row-gray-300 align-middle gs-0 gy-4'>
                          <thead>
                            <tr className='fw-bolder text-muted'>
                              <th className='w-20px'></th>
                              <th className='min-w-100px'>Aggregate Code</th>

                              <th className='min-w-150px'>Description</th>
                              <th className='min-w-100px'>Added On</th>
                            </tr>
                          </thead>

                          <tbody>
                            {tagProducts !== undefined &&
                              tagProducts.map((j) => {
                                return (
                                  <tr>
                                    <td></td>
                                    <td>
                                     
                                      <Link
                                        to={{
                                          pathname: '/product',
                                          state: {
                                            prod_id: j.prod_id,
                                            aggregate_code: j.ac4,
                                            clean_description: j.clean_description,
                                            created_at: j.created_at,
                                            product_code: j.product_code,
                                          },
                                        }}
                                      >
                                        <span className='text-danger fw-bolder text-hover-primary d-block fs-6 text-decoration-underline link'>
                                          {j.ac4 ? j.ac4 : '-'}
                                        </span>
                                      </Link>
                                    </td>

                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {j.clean_description ? j.clean_description : '-'}
                                      </span>
                                    </td>
                                    <td>
                                      <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                        {j.created_at
                                          ? moment(j.created_at).format('DD-MMMM-YYYY')
                                          : '-'}
                                      </span>
                                    </td>
                                  </tr>
                                )
                              })}
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default withRouter(TagProductWrapper)
